function smallNav() {
    let small = document.getElementById('appNavigationSmall');
    let big = document.getElementById('appNavigationBig');

    if (small) {
        small.style.display = 'flex';
    }
    if (big) {
        big.style.opacity = '0';
    }
}

function bigNav() {
    let small = document.getElementById('appNavigationSmall');
    let big = document.getElementById('appNavigationBig');

    if (small) {
        small.style.display = 'none';
    }

    if (big) {
        big.style.opacity = '1';
        document.body.style.paddingTop = 0;
    }
}

function openBottom() {
    let bottom = document.getElementById('bottomNav');
    bottom.style.display = 'flex';
}

function closeBottom() {
    let bottom = document.getElementById('bottomNav');
    bottom.style.display = 'none';
}


let over = false;
let overBottom = false;

function onScroll() {
    if (window.scrollY > 421 - 60) {
        if (!over) {
            smallNav();
            // scrollto.scrollIntoView();

            over = true;
        }
    } else {
        if (over) {
            bigNav();
            over = false;
        }
    }
    if (window.scrollY > 20) {
        if (!overBottom) {

            openBottom();
            // scrollto.scrollIntoView();

            overBottom = true;
        }
    } else {
        if (overBottom) {
            closeBottom();
            overBottom = false;
        }
    }
}




document.addEventListener('touchmove', onScroll); // for mobile
document.addEventListener("scroll", onScroll);