<template>
    <section id="bottomNav">
        <div class="container">
            <div class="navigation-list">
                <div class="navigation-list--item" v-for="(nav, index) in navList" :key="index" @click="goTo(nav.path)">
                    <img :src="nav.icon" class="icon">
                    <span class="desc">
                        {{nav.title}}
                    </span>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
       name: "BottomNavigation",
       data() {
           return {
               navList: [
                   {
                       title: 'Главная',
                       icon: require('../assets/home.svg'),
                       path: '/',
                   },
                   {
                       title: 'Специальности',
                       icon: require('../assets/spec.svg'),
                       path: '/specialty',
                   },
                   {
                       title: 'Новости',
                       icon: require('../assets/news.svg'),
                       path: '/news',
                   },
                   {
                       title: 'Вопросы',
                       icon: require('../assets/answers.svg'),
                       path: '/answers',
                   },
               ]
           }
       }, 

       methods: {
           goTo(pathRoute) {
               this.$router.push({
                   path: pathRoute,
               });
           }
       },
    }
</script>

<style lang="scss" scoped>
    #bottomNav {
        height: 54px;
        width: 100%;
        background: url('../assets/vector-opacity.png'), linear-gradient(180deg, #5383FF -11.58%, #0041EA 128.94%);
        background-position-x: center;
        //border-radius: 50px;

        position: fixed;
        bottom: 0px;

        font-size: 13px;
        color: #fff;

        display: none;
        align-items: center;
        z-index: 1000;
    }

    .navigation-list {
        display: flex;
        justify-content: space-between;
         align-items: center;
    }

    .navigation-list--item {
        display: flex;
        flex-direction: column;
        margin: 0 15px;
        cursor: pointer;
        .icon {
            height: 18px;
        }
    }
</style>