<template>
  <div id="app">
    <AppHeader :index="isindex" /> 
    <router-view/>
    <BottomNavigation />
  </div>
</template>


<script>
// @ is an alias to /src
import AppHeader from '@/components/AppHeader.vue';

import BottomNavigation from '@/components/BottomNavigation.vue';
import HomeView from '@/views/Home.vue';
export default {
  components: {
    //HelloWorld, 
    AppHeader, BottomNavigation
  },

  methods: {
    toToRoute(route) {
      this.$router.push({
        name: route
      });
    },


    CheckRoute() {
      if(this.$router.currentRoute.path != '/') {
        this.isindex = false;
        document.body.style.paddingTop = '60px';
      } else {
        this.isindex = true;
      }
    }
  },

  data() {
    return {
      isindex: true,
    }
  },
  updated() {
    
    this.CheckRoute();
  },
  mounted() {
    this.CheckRoute();
  },
};
</script>

