<template>
    <button id="button-abit" :class="{yellow: isColor('yellow'), blue: isColor('blue')}" :style="{'width': size.width, 'height': size.height}">{{text_plc}}</button>
</template>

<script>
    export default {
        props: {
            text_plc: {
                type: String,
                required: true
            },
            color: {
                type: String,
                required: false,
            },
            size: {
                type: Object,
                required: false,
            }
        },
        data() {
            return {
                activeColor: 'yellow',
                sizeBtn: {
                    width: '100%',
                    height: '45px',
                }
            }
        },
        methods: {
            colorSet() {
                if(this.color) {
                    switch(this.color) {
                        case 'yellow':
                            this.activeColor = 'yellow';
                            break;
                        case 'blue':
                            this.activeColor = 'blue';
                            break;

                    };
                }  
            },

    

            isColor(color) {
                if(color === this.activeColor) {
                    return true;
                }
                return false;
            }
        },

        mounted() {
            this.colorSet();
        },
    }
</script>

<style lang="scss" scoped>

    #button-abit {

        width: 420px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        border: none;
        padding: 0 20px;
        border-radius: 98px;
        box-sizing: border-box;
        font-family: inherit;
    
    }
    #button-abit:active {
        box-shadow: inset 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.26);
    }

    #button-abit.yellow {
        background: linear-gradient(180deg, #F9DF57 0%, #F9CC57 100%);
    }

    #button-abit.blue {
        background: linear-gradient(180deg, #0041EA 0%, #5383FF 210.71%);
        color: #fff;
    }
</style>