<template>
     <input :type="type" :placeholder="text_plc" required @keydown="dataReturn()" v-model="dataInput">
</template>

<script>
    export default {
         props: {
            text_plc: {
                type: String,
                required: true
            },
            type: {
                type: String,
                required: true,
                default: 'text'
            }
         },

         data() {
             return {
                 dataInput: null,
             }
         },

         methods: {
             dataReturn() {
                
                 this.$emit('data', this.dataInput);
             }
         },
    }
</script>

<style lang="scss" scoped>
    input {
        width: 100%;
        height: 45px;
        left: 35px;
        top: 376px;
        background: #FFFFFF;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        border: none;
        padding: 0 20px;
        border-radius: 98px;
        box-sizing: border-box;
        font-family: inherit;
    
        margin: 12px 0;
    }

    input:focus {
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.26);
    }
</style>