<template>
    <section id="select-class">
        <div class="big" v-if="type == 'big'">
            <div class="input_text">Выбери класс</div>
             
            <div class="selectClass">
                <!-- <div class="but-before" >
                    <svg class="arrow" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                        viewBox="0 0 240.823 240.823" style="enable-background:new 0 0 240.823 240.823;" xml:space="preserve">
                        <g>
                            <path id="Chevron_Right" d="M57.633,129.007L165.93,237.268c4.752,4.74,12.451,4.74,17.215,0c4.752-4.74,4.752-12.439,0-17.179
                            l-99.707-99.671l99.695-99.671c4.752-4.74,4.752-12.439,0-17.191c-4.752-4.74-12.463-4.74-17.215,0L57.621,111.816
                            C52.942,116.507,52.942,124.327,57.633,129.007z"/>
                        </g>
                    </svg>
                </div>
                -->
                <div class="selectClass--item" v-for="(item, index) in selectClassList" :key="index" :class="{active: item.active === true}" @click="setActive(index)">
                    {{item.numberClass}}
                </div>
                
               <!-- <div class="but-after" >
                    <svg class="arrow" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                        viewBox="0 0 240.823 240.823" style="enable-background:new 0 0 240.823 240.823;" xml:space="preserve">
                    <g>
                        <path id="Chevron_Right_1_" d="M183.189,111.816L74.892,3.555c-4.752-4.74-12.451-4.74-17.215,0c-4.752,4.74-4.752,12.439,0,17.179
                            l99.707,99.671l-99.695,99.671c-4.752,4.74-4.752,12.439,0,17.191c4.752,4.74,12.463,4.74,17.215,0l108.297-108.261
                            C187.881,124.315,187.881,116.495,183.189,111.816z"/>
                    </g>
                    </svg>
                </div> -->
               
            </div>
            <form action="#" style="position: relative; margin-top: 10px" @submit.prevent="startQuiz(selectClassList, name)">
                <label class="input_text" >
                    Ввведи свою электронную почту
                    <InputText text_plc="Например, example@mail.ru" v-model="name" style="margin-bottom: 45px" type="email" @data="setData"/>
                </label>
                <Button text_plc="Пройти профориентационный тест" color="yellow" :size="buttonSize" class="proftest_btn"  />
            </form>
        </div>
       
    </section>
    
</template>

<script>
    import InputText from '@/components/InputText.vue';
    import Button from '@/components/Button.vue';
    export default {
        props: ['type'],
        name: "SelectClass",
         components: {
             InputText, Button
        },
        data() {
            return {
                typeSelect: null,
                selectClassList: [
                    {
                        numberClass: 6,
                        active: false,
                    }, 
                    {
                        numberClass: 7,
                        active: false,
                    }, 
                    {
                        numberClass: 8,
                        active: false,
                    }, 
                    {
                        numberClass: 9,
                        active: true,
                    }, 
                    {
                        numberClass: 10,
                        active: false,
                    }, 
                    {
                        numberClass: 11,
                        active: false,
                    }, 
                ],
                buttonSize: {
                    width: '100%',
                    height: '45px',
                },

                name: null,

                selectClass: null,
            }
        },
        computed: {
            
        },
        methods: {
            setType() {
                this.typeSelect = this.type;
            },
            setActive(index) {
                this.selectClassList.forEach(function(element) {
                    element.active = false;
                });
                this.selectClassList[index].active = true;
            },
            setData(dataInput) {
                this.name = dataInput;
            },
            startQuiz() {
                let self = this;
                let pr = new Promise(function(resolve, reject) {
                     self.activeClass();
                     let classNum = self.selectClass;
                    resolve(classNum);
                });
            
                pr.then(classNum => {
                    localStorage.setItem('abit-class', classNum);
                    localStorage.setItem('abit-name', self.name);
                      self.$router.push({
                        name: 'quiz',
                        params: {
                            name: self.name,
                            class: classNum,
                        }
                    });
                });
                
            },
            activeClass() {
                let self = this;
                this.selectClassList.forEach(function(el) {
                    if(el['active'] === true) {
                        self.selectClass = el['numberClass'];
                    }
                });
            }
        },

        mounted() {
            this.setType();
        },
    }
</script>

<style lang="scss" scoped>
    .small_sc_btn {
        margin-left: 15px;
        font-size: 12px;
    }
    .small {
        .selectClass--item.active {
            width: 40px;
            height: 40px;
            font-size: 13px;
            font-weight: normal;
        }
    }
    .selectClass {
        display: flex;
        max-width: 410px;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }

    .proftest_btn {
        margin-top: -10px;
    }

    .selectClass--item {
        width: 47px;
        height: 47px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #171717;
        background: #FFFFFF;
        box-shadow: 0px 3px 9px rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        cursor: pointer;
        user-select: none;
    }
    .selectClass--item.active {
        width: 60px;
        height: 60px;
        font-weight: bold;
    }

    .move {
        position: absolute;
        right: 0;
        top: -35px;
    }

    .but-before, .but-after {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 15px;
        cursor: pointer;
        fill: #fff;
    }

    .but-before {
        left: -45px;
    }

    .but-after {
        right: -45px;
    }
</style>