<template>
    <section id="answerSection">
        <div class="container">
            <h3>Задай нам вопрос</h3>
            <form action="#" @submit.prevent="answerSend()">
                <label>
                    Введи свое имя
                    <InputText text_plc="Например, Алексей" class="answer-input" id="answerFormName" type="text" />
                </label>
                <label>
                    Почта, чтобы мы могли ответить
                    <InputText text_plc="Например, email@mail.ru" class="answer-input" id="answerFormEmail" type="email" />
                </label>
                <label>
                    Вопрос
                    <textarea required placeholder="Отправить вопрос" style="height: 150px; border-radius: 15px" class="answer-input answer-ta"  id="answerFormText"> </textarea>
                </label>
                <Button text_plc="Отправить вопрос" color="blue" class="about-btn" :size="buttonSize" />
            </form>
        </div>
    </section>
</template>

<script>
    import InputText from '@/components/InputText.vue';
    import Button from '@/components/Button.vue';
    let host = 'http://y93706w6.beget.tech';
    export default {
        name: "Answers",
        components: {
            InputText, Button
        },
        data() {
            return {
                validation: 'da2fa0516f6aa380a146d6fde9a3c4a5',
                buttonSize: {
                    width: '100%',
                    height: '35px',
                },
            }
        },

        methods: {
            async answerSend() {
               let text = document.getElementById('answerFormText').value;
               let name = document.getElementById('answerFormName').value;
               let email = document.getElementById('answerFormEmail').value;
               let data = {
                   name: name,
                   email: email,
                   text: text,
                   validation: this.validation,
               };
               const res = await fetch(host + '/sendmail', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(data) 
               });
                let resdata = await res.json();
               console.log(resdata);
            }
        },
    }
</script>

<style lang="scss" scoped>
    label {
        width: 100%;
        display: block;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }

    .answer-input {
        border-radius: 10px;
        width: 100%;
    }
    .answer-ta {
        resize: none;  

        width: 100%;
        line-height: 3.5em;
        font-family: inherit;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        padding: 15px 20px;
        margin: 12px 0;
        border: none;
        border-radius: 15px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        box-sizing: border-box;
    }
    .answer-ta:focus {
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.26);
    }
    .answer-ta::placeholder {
        line-height: 3.5em;
        font-family: inherit;
        font-size: 14px;
        line-height: 17px;
        color: #828282;
    }


</style>