<template>
    <section id="news-specialies">
        <div class="select">
            <div class="container">
                 <div class="select-item" :class="{active: isTab('news')}" @click="setTab('news')">
                    Новости
                </div>
                <div class="select-item" :class="{active: isTab('spec')}" @click="setTab('spec')">
                    Специальности
                </div>
            </div>
        </div>
       
        <HomeNews  v-if="isTab('news')" />
        <HomeSpecialies  v-if="isTab('spec')" />
     
    </section>
</template>

<script>
    import HomeNews from '@/components/HomeNews.vue';
    import HomeSpecialies from '@/components/HomeSpecialies.vue';
    export default {
        name: "NewsSpecialies",
        components: {
            HomeNews, HomeSpecialies
        },
        data() {
            return {
                tab: 'news',
            }
        },
        methods: {
            isTab(tab) {
                return this.tab === tab;
            },

            setTab(tab) {
                this.tab = tab;
            }
        },
    }
</script>

<style lang="scss" scoped>
    #news-specialies {
        padding-top: 45px
    }
    .select {
        font-weight: 800;
        font-size: 18px;
        line-height: 22px;
        color: #080808;

        .container {
            display: flex;
        }
    }
    .select-item {
        opacity: 0.3;
        margin-right: 25px;
        cursor: pointer;
        user-select: none;
    }

    .select-item.active {
        opacity: 1;
    }
</style>