<template>
    <div>
        <div class="container">
            <h3>
                Последние новости для абитуриентов
            </h3>
        </div>

        <div class="news-container">
            <div class="but-before" @click="incPosition">
               <svg class="arrow" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                    viewBox="0 0 240.823 240.823" style="enable-background:new 0 0 240.823 240.823;" xml:space="preserve">
                <g>
                    <path id="Chevron_Right" d="M57.633,129.007L165.93,237.268c4.752,4.74,12.451,4.74,17.215,0c4.752-4.74,4.752-12.439,0-17.179
                        l-99.707-99.671l99.695-99.671c4.752-4.74,4.752-12.439,0-17.191c-4.752-4.74-12.463-4.74-17.215,0L57.621,111.816
                        C52.942,116.507,52.942,124.327,57.633,129.007z"/>
                </g>
                </svg>
            </div>
            <div class="news-list"> 
            <!--   <transition-group name="slide" class="news-list" v-touch:swipe="swipeHandler"> -->
                
                    <div class="slider-track" :style="{transform: 'translateX(' + translate + 'px)'}" v-touch:swipe="swipeHandler">
                        <div class="news-item" v-for="(item, index) in newsListC" :key="item.id" :class="{active: active === index}" :style="{left: item.position_left+'px'}" @click="goToNews(item.id)">
                            <div class="title">
                          
                                {{item.title}}
                            </div>
                            <div class="description">
                                {{item.description}}
                            </div>
                            <div class="icon" :style="{'background-image': `url('${item.icon}')`}"></div>
                        </div>
                    </div>
                    
            <!--    </transition-group> -->
            </div> 
            <div class="but-after" @click="decPosition">
                <svg class="arrow" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                    viewBox="0 0 240.823 240.823" style="enable-background:new 0 0 240.823 240.823;" xml:space="preserve">
                <g>
                    <path id="Chevron_Right_1_" d="M183.189,111.816L74.892,3.555c-4.752-4.74-12.451-4.74-17.215,0c-4.752,4.74-4.752,12.439,0,17.179
                        l99.707,99.671l-99.695,99.671c-4.752,4.74-4.752,12.439,0,17.191c4.752,4.74,12.463,4.74,17.215,0l108.297-108.261
                        C187.881,124.315,187.881,116.495,183.189,111.816z"/>
                </g>
                </svg>
            </div>
        </div>
        
        
    </div>
</template>

<script>
    import Vue from 'vue';
    import VueCordova from 'vue-cordova'
    import {HTTP} from '../http-common'
    Vue.use(VueCordova)





    //функция-обертка для общения с api
    let host = 'https://free98781.ispsite.ru/api';


    async function getData(url = '') {
      //  console.log(cordova.plugins);
       // const response = await this.$api.get(url);
       // let res_j = await response.json();
      //  console.log(res_j);
       // return await response.json(); 
    }
    export default {
        name: "HomeNews",
        data() {
            return {
                newsList: [
                    {
                        id: 0,
                        title: "Загрузка новостей",
                        publication_date: "2019-11-01 10:15:31",
                        small_text: "Пожалуйста, подождите",
                        full_text: `Пожалуйста, подождите`,
                        icon: require('../assets/dofod.png'),
                        active: true,
                        position_left: null, 
                    },

                ],
                translate: 0,
                active: 0,

            }

        },

        methods: {


            incPosition() {
                let newsItem = document.querySelector('.news-item');
                let clientW = newsItem.clientWidth + 15;

                let sltrack = document.querySelector('.slider-track');
                if(Math.abs(this.translate) > 0 && this.active > 0) {
                    this.translate += clientW;
                    this.active-=1;
                } else {
                    console.log(sltrack.clientWidth - clientW);
                    this.translate = -(sltrack.clientWidth - clientW);
                    this.active = this.newsList.length -1;
                }
            },

            decPosition() {
                let newsItem = document.querySelector('.news-item');
                let clientW = newsItem.clientWidth + 15;

                let sltrack = document.querySelector('.slider-track');
          
                if(Math.abs(this.translate - clientW) < sltrack.clientWidth) {
                    this.translate -= clientW;
                    this.active+=1;
                } else {
                    this.translate = 0;
                    this.active=0;
                }
            },

            swipeHandler(direction, param) {
                if(direction == "left") {
                    this.decPosition();
                }
                if(direction == "right") {
                    this.incPosition();
                }
            },

            async getNews() {
                
             //  let response = await getData(host + '/getnews');
             //console.log(this.$http);
                let response = await HTTP.get('/getnews');
   
                this.newsList = response.data;
              //  this.setPosition();
            },

            goToNews(id) {
                this.$router.push({
                    name: 'newsOne',
                    params: {id},
                });
            }
        },

        mounted() {
            this.getNews();
        },

        computed: {
            newsListC() {
                return this.newsList;
            }
        },
    }
</script>

<style lang="scss" scoped>
    h3 {
        font-weight: 800;
        font-size: 16px;
        line-height: 20px;
        color: #080808;
    }

    .news-item.active {
        background: linear-gradient(180deg, #5383FF 0%, #0041EA 100%);
        color: #fff;
    }
    .news-item {
        width: 171px;
        height: 222px;
        background: #fff;
        border-radius: 15px;
        box-shadow: 0px 10px 16px rgba(0, 0, 0, 0.1), 0px 4px 6px rgba(0, 0, 0, 0.06);
        color: #080808;;
        padding: 30px 20px;
        box-sizing: border-box;
        position: relative;
        margin: 0px 7px;
        flex-shrink: 0;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      
       // position: absolute;


        .title {
            font-weight: bold;
            font-size: 21px;
            line-height: 26px;
            position: relative;
            z-index: 2;
              user-select: none;
        }

        .description {
            font-size: 16px;
            line-height: 20px;
            position: relative;
            z-index: 2;
              user-select: none;
        }

        .icon {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
            opacity: 0.3;
            user-select: none;
            background-repeat: no-repeat;
            background-position: center;
        }

    }
    .news-list {
        display: flex;
        flex-shrink: 0;
        margin: 0 auto;
        max-width: 370px;
        overflow-x: hidden;
        position: relative;
        height: 262px;
        align-items: center;
        margin-top: -20px;
    }

    .slide-leave-active,
    .slide-enter-active {
    transition: 1s;
    }
    .slide-move {
        transition: transform 0.5s;
    }

    .slider-track {
        display: flex;
        transition: 2s;
    }

    .but-before, .but-after {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 25px;
        cursor: pointer;
    }

    .but-before {
        left: -65px;
    }

    .but-after {
        right: -65px;
    }


    .news-container {
        display: flex;
        max-width: 370px;
        margin: 0 auto;
        position: relative;
    }
</style>