import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "home",
        component: Home
    },
    {
        path: "/news/:id",
        name: "newsOne",
        props: true,
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/NewsOne.vue")
    },
    {
        path: "/specialty/:id",
        name: "specialtyOne",
        props: true,
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/SpecialtyOne.vue")
    },
    {
        path: "/specialty",
        name: "specialty",
        props: true,
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/SpecialtyAll.vue")
    },
    {
        path: "/answers",
        name: "answers",
        props: true,
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/AnswersPage.vue")
    },
    {
        path: "/news",
        name: "news",
        props: true,
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/NewsAll.vue")
    },
    {
        path: "/about",
        name: "about",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/About.vue")
    },
    {
        path: "/quiz",
        name: "quiz",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/QuizPage.vue")
    }
];

const router = new VueRouter({
    mode: "hash",
    base: process.env.BASE_URL,
    routes
});

export default router;